export default function initModalsScripts() {
	$(document).on('closed', '.remodal', function (e) {
		var url = document.URL;
		var title = document.title;
		url = url.replace('#', '');
		if (window.history.replaceState) {
			window.history.replaceState(null, title, url);
		}
	});
	
	$(document).on('closed', '[data-remodal-id=modalinfo]', function (e) {
		$(this).find('.modal-wrapper').html('');
	});
	
	$(document).on('click', '.modal-btn', function() {
		let inst = $('[data-remodal-id=modalinfo]').remodal();
		let content = $(this).parents('.item').find('.wrapper').html();
		
		$('[data-remodal-id=modalinfo]').find('.modal-wrapper').html(content);
		$('[data-remodal-id=modalinfo]').find('.sliderinfo').slick({
				dots: true,
				infinite: true,
				slidesToShow: 1,
				slidesToScroll: 1,
				adaptiveHeight: true,
				prevArrow: $('[data-remodal-id=modalinfo]').find('.marr-left'),
				nextArrow: $('[data-remodal-id=modalinfo]').find('.marr-right')
		});

		$('.open-gallery').fancybox();
		
		$(window).resize();
		
		inst.open();

		return false;
	});

}


