export default function initAboutScripts() {
	if($('.main-about').length) {
		$('.slider-about').slick({
			dots: true,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			adaptiveHeight: true,
			prevArrow: $('.sla-left'),
			nextArrow: $('.sla-right')
		});
	}
	
	if($('.contact-slider').length) {
		$('.slider-contact') .slick({
			dots: true,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			adaptiveHeight: true,
			prevArrow: $('.slc-left'),
			nextArrow: $('.slc-right')
		});
	}
}


