export default function initPaginationScripts() {
	if($('.catalog-pagination').length) {

		$(document).on('click', '.catalog-pagination__item', function() {
			$('html, body').animate({
				scrollTop: $('#load-1').offset().top - $('.nav').outerHeight()
			}, {
					duration: 500
			});
			return false;
		});
		
		$(document).on('click', '.catalog-pagination__item', function() {
			if($(this).hasClass('active')) {
				return false;
			}
			
			History.pushState(null, document.title, $(this).attr('href'));
			loadPage($(this).attr('href'));
			return false;
		});
		
		var $container = $('#load-2');
		function loadPage(url) {
			$container.css({opacity: 0.5});
			$container.load(url + ' #load-2 > *', function() {
				$container.css({opacity: 1});
			});
		}
	}
}


