export default function initFiltersScripts() {
	if($('.filters').length) {
		document.addEventListener('DOMContentLoaded', function() {
			checkUrl();
		});

		$(document).on('click', '.all-cfilter,.cfilter', function() {
			$('html, body').animate({
				scrollTop: $('#load-1').offset().top - $('.nav').outerHeight()
			}, {
					duration: 500
			});
			return false;
		});
		
		$(document).on('click', '.cfilter, .sfilter', function() {
			let $id = $(this).attr('data-id');
			let $filter = $(this).attr('data-filter');
			let $href = $(this).attr('href');
			
			if($(this).hasClass('active')) {
				return false;
			}
		
			$('.cfilter').each(function (j, item) {
				$(item).removeClass('active');
			});
			
			$(this).addClass('active');
			
			History.pushState(null, document.title, $href);
			if($filter) {
				loadPage($href, $id, $filter);
			} else {
				loadPage($href);
			}
			return false;
		});
	
		function loadPage(href, id, filter) {
			let $container = $('#load-1');
			$container.css({opacity: 0.5});
			if(filter) {
				$.ajax({
							type: 'get',
							url: href,
							data:  { id: id, filter: filter },
							success: function(html){
									let $html = $(html).find('#load-2').html();

									$('#load-2').html($html);
							},
							error: function(){
									alert("Ошибка запроса");
							},
							complete: function() {
								if((window.location.pathname.search(/pamyatniki/) != -1)) {
									$('.gallery-filters-1__item').eq(1).addClass('active');
								} else if((window.location.pathname.search(/nadgrobiya/) != -1)) {
									$('.gallery-filters-1__item').eq(2).addClass('active');
								}
								$container.css({opacity: 1});
								$('.open-gallery').fancybox();
							}
					});
			} else {
				$.ajax({
							type: 'get',
							url: href,
							data:  { },
							success: function(html){
									let $html = $(html).find('#load-2').html();

									$('#load-2').html($html);
							},
							error: function(){
									alert("Ошибка запроса");
							},
							complete: function() {
								$container.css({opacity: 1});
								$('.open-gallery').fancybox();
							}
					});
			}
		}
		
		//notusingfunction
		function display(elem) {
			let check = false;
			$('#err').html('');
			$('.item').each(function (j, good) {
				$(good).hide().css('display', 'none');
				
				var isItemMeetsFilterGroup1 = false;
				
				let attr = $(good).attr('data-filter');
				$(elem).each(function (i, filter) {
					if (typeof attr !== typeof undefined && attr !== false) {
						if(attr.trim()) {
							if (attr.indexOf($(filter).attr('data-id')) != -1) {
								isItemMeetsFilterGroup1 = true;
								return false;
							}
						}
					}
				});
				if (isItemMeetsFilterGroup1) {
					$(good).show().css('display', 'block');
				}
				
				if($(good).attr('style') == 'display: block;') {
					check = true;
					$('#err').html('');
				}

				if(check == false) {
					$('#err').html('<p сlass="warn" style="padding: 20px 0 50px 0; color: red; font-size: 20px;"> По вашему запросу ничего не найдено.</p>');
				}
			});
		}
		
		function checkUrl() {
			$('.cfilter').each(function(i, item) {
				if($(item).attr('href') == window.location.pathname) {
					if(window.location.search) {
						return false;
					} else {
						let $id = $(item).attr('data-id');
						let $filter = $(item).attr('data-filter');
						let $href = $(item).attr('href');
						if($filter) {
							loadPage($href, $id, $filter);
							return false;
						} else {
							loadPage($href);
							return false;
						}
					}
				}
			});
			$('.sfilter').each(function(i, item) {
				if($(item).attr('href') == window.location.pathname) {
					if(window.location.search) {
						if((window.location.pathname.search(/pamyatniki/) != -1)) {
							$('.gallery-filters-1__item').eq(1).addClass('active');
						} else if((window.location.pathname.search(/nadgrobiya/) != -1)) {
							$('.gallery-filters-1__item').eq(2).addClass('active');
						}
						return false;
					} else {
						let $id = $(item).attr('data-id');
						let $filter = $(item).attr('data-filter');
						let $href = $(item).attr('href');
						loadPage($href, $id, $filter);
						return false;
					}
				}
			});
		}
	}
}


