import initNavScripts from '../blocks/nav/nav.js'
import initAboutScripts from '../blocks/main-about/about.js'
import initModalsScripts from '../blocks/modals/modals.js'
import initFiltersScripts from '../blocks/gallery/filters.js'
import initPaginationScripts from '../blocks/catalog/pagination.js';


;(() => {
	initNavScripts();
	initAboutScripts();
	initModalsScripts();
	initFiltersScripts();
	initPaginationScripts();
	
	$(document).on('click', '.actions-offer__btn', function() {
		$('[data-remodal-id=action]').find('input[name="form"]').val('Акция - ' + $(this).parents('.actions-offer').find('.actions-offer__title').html());
	});
	
	$('.actions-offer__btn').hover(
		function(){
			$(this).parents('.actions-offer').find('img').addClass('active');
		},
		function(){
			$(this).parents('.actions-offer').find('img').removeClass('active');
	 });
	
	//helper
	function setCookie(name, value, options) {
		options = options || {};
		var expires = options.expires;
		if (typeof expires == "number" && expires) {
			var d = new Date();
			d.setTime(d.getTime() + expires * 1000);
			expires = options.expires = d;
		}
		if (expires && expires.toUTCString) {
			options.expires = expires.toUTCString();
		}
		var val = encodeURIComponent(value);
		var updatedCookie = name + "=" + val;
		for (var propName in options) {
			updatedCookie += "; " + propName;
			var propValue = options[propName];
			if (propValue !== true) {
				updatedCookie += "=" + propValue;
			}
		}
		document.cookie = updatedCookie;
	}

	$('input[type="tel"]').mask('+7(999)999-99-99');
		
	$(document).on('focus click', '.required', function() {
		$(this).removeClass('error');
	});
	
	//handler
	$(document).on('click', 'button[type="submit"]', function () {

		const $form =  $(this).parents('form');
		let $btn = $(this);
		let errors = false;
		
		$form.find('.required').each(function() {
			let $val = $(this).val();
				if(!$val) {
					$(this).addClass('error');
					errors = true;
				}
			});

		if(errors == false) {
			yaCounter.reachGoal('order');
			
			let $btnText = $btn.text();
			$btn.text('Обработка данных...');

			const $method = $form.attr('method');
			const $action = $form.attr('action');
			const formData = new FormData($(this).parents('form')[0]);

			$.ajax({
				type: $method,
				url: $action,
				data: formData,
				processData: false,
				contentType: false,
				success: function(data) {
					setCookie('empty', 'val', { path: '/' });
					window.location.href = '/thank.html';
					$btn.text($btnText);
				},
				error: function(data) {
					$btn.text('Ошибка');
					setTimeout(function() {
						$btn.text($btnText);
					}, 2000);
				}
			});
		} else {
			console.log('Error');
		}
		return false;
	});
	
	function loadFiles(data) {
			var sendData = '';
	
			if (data) {
				sendData = data;
			} else {
				sendData = '';
			}
			$.ajax({
				url: '/assets/templates/content/upload.php',
				type: 'POST',
				data: sendData,
				cache: false,
				dataType: 'json',
				processData: false,
				contentType: false,
				xhr: function xhr() {
					var xhr = $.ajaxSettings.xhr();
					xhr.upload.addEventListener('progress', function (evt) {
						if (evt.lengthComputable) {
							$('.progress .progress-bar').slideDown();
							var progress = parseInt(evt.loaded / evt.total * 100);
							$('.progress .progress-bar').css('width', progress + '%');
						}
					}, false);
					return xhr;
				},
				success: function success(respond, textStatus, jqXHR) {
					if (typeof respond.error === 'undefined') {
						setTimeout(function () {
							$('.progress .progress-bar').slideUp();
							$('.progress .progress-bar').css('width', '0');
						}, 600);
						var files_path = respond;
						var html = '';
						$.each(files_path, function (key, val) {
							html += '<div class="file-item"><div class="text">' + val +'</div><div class="delfile"></div></div>';
						});
						$('.files').html(html);
					} else {
						console.log('Ошибка°: ' + respond.error);
					}
				},
				error: function error(jqXHR, textStatus, errorThrown) {
					console.log('Ошибка°: ' + textStatus);
				}
			});
		}
	
		document.addEventListener('DOMContentLoaded', function () {
			loadFiles();
			if(window.location.pathname.search(/ogrady/) != -1) {
					$('.char li:eq(3)').addClass('another');
					return false;
			}
			$('.open-gallery').fancybox();
		});
	
		var files = '';
	
		$(document).on('change', 'input[type=file]', function () {
	
			files = this.files;
	
			var data = new FormData();
	
			$.each(files, function (key, value) {
				data.append(key, value);
			});
	
			loadFiles(data);
		});
	
		$(document).on('click', '.delfile', function () {
			var self = this;
			var name = $(self).parent().find('.text').html();
			$('#nameDel').val(name);
			var data = $(this).parents('form').serialize();
			$.ajax({
				url: '/assets/templates/content/delete.php',
				type: 'POST',
				data: data,
				cache: false,
				success: function success(respond, textStatus, jqXHR) {
					if (respond == 'success') {
						$(self).parent().remove();
						$('input[type=file]').val('');
					}
				},
				error: function error(respond, textStatus, errorThrown) {
					console.log(textStatus);
				}
			});
		});
	
})();



